body {
    margin: 0;
    padding: 0;
    background-color: white;
}

.content {
    width: 100%;
    /* height: fit-content; */
    position: relative;
}

.content__container {
    width: 100vw;
    height: 100vh;
    background-color: transparent;
    position: absolute;
}

.content__container canvas {
    position: absolute;
}

.content__section {
    width: 100vw;
    height: 100vh;
    position: relative;
    background-color: white;
}

.content__section--orange{
    background-color: #f0712c;
}